export default{
    props:{
        list:{
            type:Array,
            default:[]
        }
    },
    data(){
        return{
           selectedVal:''
        }
    },
    mounted(){
       this.$on('reset',()=>{
           this.selectedVal=this.list[0].val
       })
    },
    created(){
      this.selectedVal=this.list[0].val
    },
    methods:{
        handleSelect(e){
            var id=e.target.dataset.id||'';
            var text=e.target.dataset.text || '';
            this.selectedVal==id?id='repeat'+Math.random():this.selectedVal=id;
            this.$emit('input',id);
            this.$emit('on-change',text)           
        }
    }
}



